:root {
  --white: #fff;
  --black: #000;
  --blue: #00acdc;
  --shadow: #0000000d;
}

body {
  margin: 0;
}

.header {
  background: var(--white);
  box-shadow: 0 2px 4px var(--shadow);
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo__link {
  width: 40px;
  height: 40px;
  display: block;
}

.logo__img {
  width: 100%;
  height: 100%;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  white-space: nowrap;
  color: var(--black);
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.nav__link:hover, a.is-active {
  color: var(--blue);
}

a.is-active:after {
  content: "";
  background: var(--blue);
  color: var(--blue);
  border-radius: 2px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

